<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card id="loading-container" class="vs-con-loading__container mb-base">
      <div class="flex justify-between">
        <h4 class="mb-4">{{ $t('SEARCH_CRITERIA') }}</h4>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('ACCOUNT') }}</label>
          <account-lookup :title="$t('ACCOUNT')" valueField="accountNumber" displayField="accountNumber" @select-record="selectAccountRecord"/>
        </div>
      </div>

      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-end">
            <vs-button class="py-3" @click="showCheques">{{ $t('SUBMIT') }}</vs-button>
          </div>
        </div>
      </div>
    </nv-card>
    <div v-if="chequeTableVisible && !chequeTableBusy">
      <component :is="`IssuedCheque${dataLayout}`" :key="Date.now()" :parent="this"/>
    </div>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import IssuedChequeListLayout from '@/views/cheque/issued-cheque/issued-cheque-browse/IssuedChequeListLayout'
import IssuedChequeCardLayout from '@/views/cheque/issued-cheque/issued-cheque-browse/IssuedChequeCardLayout'
import IssuedChequePivotLayout from '@/views/cheque/issued-cheque/issued-cheque-browse/IssuedChequePivotLayout'
import IssuedChequeTableMixin from '@/mixins/cheque/IssuedChequeTableMixin'
import AccountLookup from '@/views/account/account-root/AccountLookup'

export default {
  name: 'issued-cheque-browse',
  mixins: [IssuedChequeTableMixin, AlertMixin],
  components: {
    IssuedChequeListLayout,
    IssuedChequeCardLayout,
    IssuedChequePivotLayout,
    AccountLookup
  },
  data: function () {
    return {
      dataLayout: 'ListLayout',
      accountNumber: '',
      chequeTableVisible: false
    }
  },
  methods: {
    showCheques () {
      if (this.accountNumber) {
        this.chequeTableVisible = true
        this.loadChequeTableData()
      }
    },
    selectAccountRecord (record) {
      this.accountNumber = record.accountNumber
    }
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
